import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';


@Component({
  selector: 'app-details',
  templateUrl: './details.page.html',
  styleUrls: ['./details.page.css'],
})
export class DetailsPage implements OnInit {
  contents
  language: string
  len: number
  index: number
  data
  constructor(private route: ActivatedRoute, private location: Location) {

console.log(this.data)
  }

  ngOnInit() {
  
    this.route.queryParams.subscribe((res) => {
    this.contents = JSON.parse(res.value); this.language = res.language; console.log(JSON.parse(res.value)); this.data = JSON.parse(res.data);
    });
  }


  goBack() {
    this.location.back();

  }


  back(index) {
    this.index = index - 1
    this.contents = this.data.filter(s => s.index == this.index)[0]
     

  }

  next(index) {
    this.index = index + 1
    this.contents  = this.data.filter(s => s.index == this.index)[0]

  }

 

}
